<template>
   <div>
        <Table :columns="columns" :data="data"></Table>
        <Page class="page" @on-change="toPage" :total="allData.length" />
   </div>
</template>
<script>
    import axios from "axios";
    export default {
        data () {
            return {
                columns: [
                    {
                        title: '业务编号',
                        key: 'business_number'
                    },
                    {
                        title: '业务类型',
                        key: 'business_type'
                    },
                    {
                        title: '机器编号',
                        key: 'machine_number'
                    },
                    {
                        title: '业务状态',
                        key: 'business_status'
                    },
                    {
                        title: '单价',
                        key: 'money'
                    },
                    {
                        title: '时长',
                        key: 'length'
                    }
                ],
                data: [
                ],
                allData: [],
                page: 0,
                pageTotal: 11,
            };
        },
        methods: {
            getData() {
                 axios.get("http://"+location.hostname+"/customer/businessList").then(res => {
                    if(res.data.code==1) {
                        this.allData = res.data.data;
                        this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                    }else if(res.data.code==5000) {
                        location.href = "http://"+location.hostname+"/tz/login.html";
                    } else {
                        console.error(res.data.msg);
                    }
                });
            }
        },
        mounted() {
           this.getData();
        }
    }
</script>